import { useState } from "react";
import { uniqueTitles } from "./helpers/inventory";
import Cart from "./components/Cart";
import Product from "./components/Product";
import Shopper from "./components/Shopper";
import './App.css';

function App() {
  const [cart, setCart] = useState([]);
  const uniques = uniqueTitles({type: "mixed"});

  return (
    <>
      <div className="row">
        <div className="column">
          <Shopper cart={cart} setCart={setCart} />
        </div>
      </div>
      <div className="row">
        <div className="App column">
          {uniques.map(value => {
            return (<><Product title={value} cart={cart} setCart={setCart} /></>);
          })}
        </div>
        <div className="column">
          <div className="Cart">
            <Cart datas={cart} />
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
