{"type": "case", "title": "Autumn Galaxy-hopped Seasonal Double IPA", "price": "115.20", "quantity": "24", "beer": "Autumn", "debug": "This listing is for one case of 24 cans. This beer is blowing us away. It is outstanding.  It tastes like ripe pineapple, orange sherbet, and mango sugar with minimal bitterness. Galaxy at it\u2019s absolute best. The story of our homage to an incredible place:Brewing a Galaxy hop-filled Double IPA with the name Autumn seems contrary to tradition. With the name Autumn, one might think copper-colored lagers, sessionable porters, and pumpkin-spiced ales of all shapes and sizes\u2026 so let me explain.In the Australian Autumn of 2019, in March, we ventured to Australia to meet the folks behind these hops for the first time. Having brewed with Galaxy for almost seven years, it was exhilarating to get the opportunity to travel to the other side of the world in search of context and inspiration.What we encountered when we got there was well beyond our wildest expectations. Aside from the energy of the people, the immeasurable beauty of the coastline and countryside, and the eclectic culture of culinary delight, what inspired us the most was the focus and dedication of the folks as Rostrevor Hop Gardens in Victoria and Bushy Park Estates in Tasmania.Most of the operations are overseen by generational farmers who have been working the land for over 100 years. Their deep understanding of climate, seasonal shifts, and hands-on adjustment lends itself to a consistent, high quality, immensely distinctive hops that are enjoyed in every corner of the world.We had the opportunity to enjoy Very Green, brewed with roughly 60% Galaxy, three days after it was canned an entire world away with the scent of just-plucked Galaxy cones in our hands with the sun shining on our face in the middle of one of the most pristine valleys on Earth. To call it memorable would be an understatement - it was transcendent. Talk about full circle. It provided us with a lifetime of inspiration to honor the work of the folks who make a hop like this possible. For the rest of our lives, each time we drink a beer with hops from Rostrevor or Bushy Park we will be instantly transported back to the memory of a place that is full of heart, soul, focus, immeasurable beauty, and dedication. Our hope is that with Autumn - brewed with 100% Galaxy hops paired with our house yeast - we are able to share that story, and bring a little bit of that experience, back home with you in each can.Enjoy."},
{"type": "case", "title": "Haze Flagship Double IPA", "price": "103.20", "quantity": "24", "beer": "Haze", "debug": "This listing is for one case of 24 cans of Haze.HAZE  (Double IPA - 8.2%) - Our Double IPA! We smell a ton of peach on the nose, with complementary notes of orange, mango, and passionfruit. The flavor is similar to a blast of citrus fruit & peach nectar quickly followed by a bounty of tropical fruit.  A lingering and pleasant saturated hop oil finish awaits. Haze is the distillation of years of focused hands-on brewing endeavor at Tree House... It is an unfiltered and intensely kettle and dry-hopped Tree House Double IPA - a real juice bomb of a beer and one that gets better and better as you work your way through the glass."},
{"type": "four", "title": "Crew Beer Crushable Pale Ale [4-Pack]", "price": "14.20", "quantity": "4", "beer": "Crew Beer Crushable Pale Ale [4-Pack]"},
{"type": "case", "title": "Julius Flagship Tree House IPA", "price": "91.20", "quantity": "24", "beer": "Julius", "debug": "This listing is for one case of 24 cans of Julius.JULIUS (American IPA - 6.8% ABV) - Bursting with fresh hand-selected American hops, Julius - our flagship IPA - is a bright, juicy beer filled with flavors and aromas of mango, peach, passionfruit, and a melange of citrus juice. A soft, pillowy mouthfeel and rounded bitterness ensure it will never tire the palate. Loaded with immense flavors as a result of years of focused brewing application and judicious use of the freshest hand-selected hops, Julius is a delicious Tree House IPA that is a true joy to drink time and time again."}
